*{
          font-family: var(--font-family); 
}

.gpt3__navbar {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 2rem 6rem;
}

.gpt3__navbar-links {
          flex: 1;
          display: flex;
          justify-content: flex-start;
          align-items: center;
}

.gpt3__navbar-links_logo {
          margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
          width: 62px;
          height: 16px;
}

.gpt3__navbar-links_container {
          display: flex;
          flex-direction: row;
          /* width: 566px;
          height: 25px; */
}

.gpt3__navbar-sign {
          display: flex;
          justify-content: flex-end;
          align-items: center;
}

.gpt3__navbar-links_container a,
.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p,
.gpt3__navbar-menu_container a
{
          text-decoration: none;
          color: #FFF;
          font-family: var(--font-family);
          font-size: 18px;
          font-style: normal;
          font-weight: 500;
          line-height: 25px;
          text-transform: capitalize;
          
          margin: 0 1rem;
          cursor: pointer;

}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button{
          font-size: 18px;
          font-weight: 500;
          line-height: normal;
          background: #FF4820;
          color: #FFF;

          width: 152px;
          height: 58px;
          border-radius: 5px;
          border: 0;
          outline: none;
          cursor: pointer;
}

.gpt3__navbar-menu {
          margin-left: 1rem;
          display: none;
          position: relative;
}

.gpt3__navbar-menu svg {
          cursor: pointer;
}

.gpt3__navbar-menu_container {
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
          align-items: flex-end;

          text-align: end;
          background: var(--color-footer);
          padding: 2rem;
          position: absolute;
          top: 40px;
          right: 0;
          margin-top: 1rem;
          min-width: 210px;
          border-radius: 5px;
          box-shadow: 0 0 5 rgba(0, 0, 0, 0.22);
}

.gpt3__navbar-menu_container p {
          margin: 1rem 0;
}

.gpt3__navbar-menu_container-links-sign {
          display: none;
}

@media screen and (max-width:1050px) {
          .gpt3__navbar-links_container {
                    display: none;
          }

          .gpt3__navbar-menu {
                    display: flex;
          }
}

@media screen and (max-width:650px) {

          .gpt3__navbar {
                    padding: 2rem ;
          }

          .gpt3__navbar-sign {
                    display: none;
          }

          .gpt3__navbar-menu_container {
                    top: 20px;
          }

          .gpt3__navbar-menu_container-links-sign {
                    display: block;
          }

}